import { Component, OnInit } from '@angular/core';
import { UserData } from 'src/app/interfaces/interfaces';
import { UsersService } from '../../services/users.service';
import { Camera, CameraOptions } from '@awesome-cordova-plugins/camera/ngx';

declare var window: any;
@Component({
  selector: 'app-personal',
  templateUrl: './personal.page.html',
  styleUrls: ['./personal.page.scss'],
})
export class PersonalPage implements OnInit {

  user: UserData;
  dateUser: Date;
  tempImg: string = '../../../assets/img/userDefault.png';
   get dataUser() : UserData {

    return  this.userservice.getUser;
  }

  constructor(private userservice: UsersService, 
              private camera: Camera) { }

   ngOnInit() {
    setTimeout(() => {

      this.user = this.dataUser;
      this.dateUser = new Date(this.user.birth);
    }, 500);
    
  }

  camara() {
    console.log('entro en funcion de camara');
    
    const options: CameraOptions = {
      quality: 50,
      destinationType: this.camera.DestinationType.FILE_URI,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      correctOrientation: true,
      sourceType: this.camera.PictureSourceType.CAMERA
    };

    this.camera.getPicture(options).then( ( imageData ) => {
    //  let base64Image = 'data:image/jpeg;base64,' + imageData;
    const img = window.Ionic.WebView.convertFileSrc(imageData);
      console.log('imagen de la camara',img);
      
     this.tempImg = img;
     }, (err) => {
      // Handle error
     });
  }

}
